import { useState } from 'react';

export const useTheme = () => {
  const setMode = (mode: any) => {
    //   localstorage        
    window.localStorage.setItem('theme', mode);
    setTheme(mode);
  };

  const toggleTheme = () => setMode(theme === 'dark' ? 'light' : 'dark');

  let initTheme!: string;
  if (typeof window !== 'undefined') {
    //    
    const isBrowserDarkMode = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;

    //        
    const localSettingTheme = window.localStorage.getItem('theme');

    //            
    if (isBrowserDarkMode && !localSettingTheme) {
      initTheme = 'dark';
    } else if (localSettingTheme) {
      initTheme = localSettingTheme;
    } else {
      initTheme = 'light';
    }
  }
  const [theme, setTheme] = useState(initTheme);

  return { themeMode: theme, toggleTheme };
};
